.default {
    height: 42px;
    width: 85px;
    border: none;
    background: darkseagreen;
    color: white;
    padding: 10px;
    font-size: 18px;
    border-radius: 5px;
    position: relative;
    box-sizing: border-box;
    transition: all 500ms ease;
    margin: 5px;
    overflow: hidden;
    cursor: pointer;
}

.default:active {
    background: #45c645;
}

.default:disabled {
    background-color: grey;
    color: #969595;
    cursor: auto;
}

button:focus {
    outline: none;
}

.red {
    height: 42px;
    width: 85px;
    border: none;
    background: #fd9999;
    color: white;
    padding: 10px;
    font-size: 18px;
    border-radius: 5px;
    position: relative;
    box-sizing: border-box;
    transition: all 500ms ease;
    overflow: hidden;
    cursor: pointer
}

.red:active {
    background: #ff2626;
}

.red:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 42px;
    background: rgba(255, 0, 0, 0.3);
    border-radius: 5px;
    transition: all 2s ease;
}

.red:hover:before {
    width: 100%;
}



